const uni = require('uni').default;





































var app= require("../../common/common.js"); 
var id;
export default{
	data:{
		pageLoad:false, 
		pageData:{},
		yzm:"",
		telephone:""
	},
	onLoad:function(option){
		id=option.id;
		this.getPage();
	},
	 
	methods:{
		getYzm:function(){
				uni.request({
					url:app.apiHost+"?m=register&a=SendSms&ajax=1",
					data:{
						telephone:this.telephone,
						fromapp:app.fromapp()
					},
					success:function(res){
						uni.showToast({
							title:res.data.message,
						})
					}
				})
		},
		getPage:function(){
			var that=this;
			uni.request({
				url:app.apiHost+"?m=register&a=ajax&fromapp="+app.fromapp(),
				data:{
					authcode: app.getAuthCode()
				},
				success:function(data){
					that.pageLoad=true;
					that.pageData=data.data.data;
					 
				}
			})
		},
		formSubmit:function(e){
			console.log(JSON.stringify(e.detail.value));
			uni.request({
				url:app.apiHost+"?m=register&a=regsave&ajax=1",
				method:"POST",
				header:{
					"content-type":"application/x-www-form-urlencoded"
				},
				data:e.detail.value,
				success:function(res){
					var data=res.data;
					if(res.data.error){
						uni.showToast({
							"title":res.data.message
						})
					}else{
						app.setAuthCode(data.data.authcode);
						app.goHome();
					}
					
				}
			})
			
		}
	},
}



